import React from 'react';

class BasicInput extends React.Component {
    constructor(props) {
        super(props)
        
        this.state = {
            input: '',
            value: ''
        };

        this.buttonRef = React.createRef();
        this.formRef = React.createRef();

        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleClick() {
        this.buttonRef.current.classList.add('hidden');
        this.formRef.current.classList.remove('hidden');
    }

    handleChange(e) {
        this.setState({
            input: e.target.value
        });
    }

    handleSubmit(e) {
        e.preventDefault();
        this.buttonRef.current.classList.remove('hidden');
        this.formRef.current.classList.add('hidden');
        this.setState({
            value: this.state.input,
            input: ''
        });
    }

    render() {
        let label = (this.state.value==='') ? this.props.name : this.state.value;
        let svg = (this.props.name==='Phone') ? <div className='inline pr-2'><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className='inline' viewBox="0 0 16 16">
                                                    <path d="M3.654 1.328a.678.678 0 0 0-1.015-.063L1.605 2.3c-.483.484-.661 1.169-.45 1.77a17.568 17.568 0 0 0 4.168 6.608 17.569 17.569 0 0 0 6.608 4.168c.601.211 1.286.033 1.77-.45l1.034-1.034a.678.678 0 0 0-.063-1.015l-2.307-1.794a.678.678 0 0 0-.58-.122l-2.19.547a1.745 1.745 0 0 1-1.657-.459L5.482 8.062a1.745 1.745 0 0 1-.46-1.657l.548-2.19a.678.678 0 0 0-.122-.58L3.654 1.328zM1.884.511a1.745 1.745 0 0 1 2.612.163L6.29 2.98c.329.423.445.974.315 1.494l-.547 2.19a.678.678 0 0 0 .178.643l2.457 2.457a.678.678 0 0 0 .644.178l2.189-.547a1.745 1.745 0 0 1 1.494.315l2.306 1.794c.829.645.905 1.87.163 2.611l-1.034 1.034c-.74.74-1.846 1.065-2.877.702a18.634 18.634 0 0 1-7.01-4.42 18.634 18.634 0 0 1-4.42-7.009c-.362-1.03-.037-2.137.703-2.877L1.885.511z"/>
                                                </svg></div>
                : ((this.props.name==='Email') ? <div className='inline pr-2'><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="inline" viewBox="0 0 16 16">
                                                    <path d="M0 4a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v8a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V4zm2-1a1 1 0 0 0-1 1v.217l7 4.2 7-4.2V4a1 1 0 0 0-1-1H2zm13 2.383l-4.758 2.855L15 11.114v-5.73zm-.034 6.878L9.271 8.82 8 9.583 6.728 8.82l-5.694 3.44A1 1 0 0 0 2 13h12a1 1 0 0 0 .966-.739zM1 11.114l4.758-2.876L1 5.383v5.73z"/>
                                                </svg></div>
                : '') 
        return (
            <div className='my-1.5'>
                <p className={this.props.size==='normal' ? 'text-l inline' : 'text-5xl inline'}>{svg}{label}</p>
                <button className='mx-2 bg-white shadow-md px-2 py-0.5' onClick={this.handleClick} ref={this.buttonRef}>Edit</button>
                <form className='hidden inline mx-2' onSubmit={this.handleSubmit} ref={this.formRef}>
                    <input type='text' onChange={this.handleChange}/>
                    <button className='mx-2 bg-white shadow-md px-2 py-1' type='Submit'>Submit</button>
                </form>
            </div>
        );
    }
}

export default BasicInput;